  font-family: "PMNCaeciliaSansHead";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___})
    format("truetype");
  font-weight: 100;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "PMNCaeciliaSansHead", sans-serif;
  text-decoration: none;
  font-weight: normal;
}

.sidebar {
  background-color: var(--sidebar-background-color);
  padding: 5px 5px 5px 0;
  padding-top: 140px;
  width: 25%;
  height: 100vh;
  position: sticky !important;
  top: 0;
  overflow-y: auto;
}

.sidebarStart,
.sidebarMenu {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
  cursor: pointer;
  position: relative;
}

.sidebarStart.active::before,
.sidebarMenu.active::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0.6rem;
  height: 3.8rem;
  background-color: var(--primarycolor);
  transition: top 0.3s ease;
}

.sidebarStart span,
.sidebarMenu .menuContent span {
  font-size: 20px;
  color: var(--secondary-color);
  transition: color 0.3s ease;
}

.sidebarStart.active span,
.sidebarMenu.active .menuContent span {
  color: var(--primarycolor);
}

.homeIcon,
.menuIcon,
.angledownIcon {
  width: 30px;
  height: 25px;
  margin-right: 10px;
  color: var(--secondary-color);
  transition:
    color 0.3s ease,
    transform 0.3s ease;
}

.angledownIcon {
  width: 25px;
  height: 30px;
  margin-left: 10px;
}

.sidebarStart.active .homeIcon,
.sidebarMenu.active .menuIcon,
.sidebarMenu.active .angledownIcon {
  color: var(--primarycolor);
}

.sidebarMenu.show .angledownIcon {
  transform: rotate(180deg);
}

.sidebarStart a,
.menuContent {
  display: flex;
  align-items: center;
  width: 100%;
  padding-left: 20px;
}

.submenu {
  display: none;
  flex-direction: column;
  margin-top: 10px;
  padding-left: 60px;
  font-size: 15px;
}

.sidebarMenu.show .submenu {
  display: flex;
}

.submenu a {
  text-decoration: none;
  color: var(--secondary-color);
  transition: color 0.3s ease;
}

.submenu a:hover {
  color: var(--primarycolor);
}

.submenu span {
  font-size: 16px;
  padding: 5px 0;
}

.sidebar-button-container {
  display: none;
  flex-direction: column;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background: var(--primarycolor);
  color: var(--sidebar-background-color);
  padding: 8px;
  text-decoration: none;
  z-index: 1000;
}

.skip-link:focus {
  top: 0;
}

.sidebarOverlay {
  position: fixed;
  inset: 0;
  z-index: 40;
  background-color: black;
  opacity: 50%;
}

/* Responsive styles */
@media (max-width: 1200px) {
  .sidebar {
    display: none;
    position: fixed;
    z-index: 100;
  }

  .sidebar.visible {
    display: block;
  }

  .sidebar-button-container {
    display: flex;
  }
}

@media (max-width: 1000px) {
  .sidebarStart span,
  .sidebarMenu .menuContent span,
  .submenu a {
    font-size: 14px;
  }
}
